<template>
    <div>
        <Header :choice="1" :state="state"></Header>
        <div class="container" :class="{ 'setOpacity': edit }">
            <div class="wrapper">
                <div class="cover">
                    <img src="../../public/img/laptop-min.jpg" alt="laptop" class="cover-img">
                </div>
                <div class="user-wrapper">
                    <img src="../../public/img/profile.png" alt="profile" class="profile-img">
                    <div class="user-info">
                        <div class="email">{{ user.email }}</div>
                        <div class="name">{{ user.name }}</div>
                        <div class="age">{{ user.age }} years old</div>
                    </div>
                    <div class="user-edit">
                        <button class="user-edit-btn" @click="editPopup">Edit</button>
                    </div>
                </div>
                <div class="blogs-wrapper">
                    <ArticleList :articles="blogs" mode="modify"></ArticleList>
                    <div class="see-more-btn" @click="seeMoreHandler" v-if="ableToLoadMore">See more</div>
                </div>
            </div>
        </div>
        <!-- popup edit -->
        <div id="edit-popup-container">
            <form class="edit-popup-form">
                <div class="edit-title">Edit</div>
                <hr>
                <input type="text" v-model="user.name" placeholder="Name">
                <input type="text" v-model="user.age" placeholder="Age">
                <input type="password" v-model="user.password" placeholder="Password">
                <hr>
                <div class="edit-popup-btn-wrapper">
                    <button class="edit-popup-btn edit-popup-submit-btn" @click="editSubmitHandler">Submit</button>
                    <button class="edit-popup-btn edit-popup-cancel-btn" @click.prevent="editCancelHandler">Cancel</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import Header from "../components/Header";
import userRequest from "../request/user";
import blogRequets from "../request/blog";
import ArticleList from "../components/ArticleList";
export default {
    props: {
        state: {
            type: Object
        }
    },
    components: {
        Header,
        ArticleList
    },
    data: function () {
        return {
            user: {},
            blogs: [],
            edit: false,
            limit: 7,
            skip: 0,
            ableToLoadMore: false
        }
    },
    created: async function () {
        const { user } = await userRequest.getUserFromToken();
        const { blogs } = await blogRequets.getBlogsOfUser(this.limit, this.skip);
        this.user = user;
        this.blogs = blogs;
        // check if we can load more articles
        if (this.blogs.length % this.limit !== 0 || this.blogs.length === 0) {
        this.ableToLoadMore = false;
        } else {
            this.ableToLoadMore = true;
        }
    },
    methods: {
        editPopup: function () {
            const style = document.getElementById("edit-popup-container").style;
            style.display = "flex";
            style.justifyContent = "center";
            style.alignItems = "center";
            this.edit = true;
        },
        editSubmitHandler: async function () {
            document.getElementById("edit-popup-container").style.display = "none";
            this.edit = false;
            await userRequest.updateUser(this.user);
        },
        editCancelHandler: function () {
            document.getElementById("edit-popup-container").style.display = "none";
            this.edit = false;
        },
        seeMoreHandler: async function () {
            this.skip += this.limit;
            const { blogs: moreBlogs } = await blogRequets.getBlogsOfUser(this.limit, this.skip);
            this.blogs = this.blogs.concat(moreBlogs);
            // check if we can load more blogs
            if (this.blogs.length % this.limit !== 0 || moreBlogs.length === 0) {
                this.ableToLoadMore = false;
            } else {
                this.ableToLoadMore = true;
            }
        }
    }

}
</script>

<style scoped>
.container {
    background-color: #f2f2f2;
    padding: 20px 0px;
}

.wrapper {
    width: 60%;
    margin:auto;
}

.cover{
    height: 200px;
    overflow: hidden;
}

.cover-img {
    width: 100%;
}

.user-wrapper {
    display: flex;
    padding: 10px 0px;
    margin-bottom: 10px;
    background-color: #FFF;
}

.profile-img {
    width: 15%;
}

.user-info {
    width: 75%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    
}

.user-edit {
    width: 10%;
    display: flex;
    align-items: center;
}

.user-edit-btn {
    background: #FFF;
    border: 1px solid #CCC;
    color: #2FB5FA;
    border-radius: 20px;
    padding: 8px 10px 8px 10px;
}

.user-edit-btn:hover {
    color: #FFF;
    background-color: #2FB5FA;
}

.email {
    color: #2FB5FA;
    font-weight: 600;
}

.name {
    color: #be4b49;
}

.age {
    color: #999999;
}

.blogs-wrapper {
    background-color: #f2f2f2;
}

#edit-popup-container {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    z-index: 10;
}

.edit-popup-form {
    margin: 0px;
    padding: 10px;
    /* background-color: #ffffff; */
    background-color: #242526;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
    width: 40%;
    border-radius: 6px;
}

.edit-popup-form > input {
    width: 100%;
    padding: 10px;
    border: 1px solid #888888;
    border-radius: 6px;
    margin-bottom: 20px;
    background-color: #f5f6f7; 
}

.edit-popup-btn-wrapper {
    padding: 5px;
    display: flex;
    justify-content: flex-end;
}

.edit-popup-btn {
    padding: 8px 10px;
    border-radius: 6px;
    border: none;
}

.edit-popup-submit-btn {
    margin-right: 15px;
    background-color: #00a400;
    color: #ffffff;
}

.edit-title {
    font-size: 32px;
    color: #ffffff;
}

.see-more-btn {
    cursor: pointer;
    color: #2fb5fa;
    text-align: center;
}

hr {
    margin: 20px 0px; 
}

.setOpacity {
    opacity: 0.4;
}

@media screen and (max-width: 665px) {
    .wrapper {
        width: 100%;
    }
}

</style>