<template>
    <div class="container">
        <div class="introduction">
            <h1>Pencil</h1>
            <p>Knowledge is power. Share your power!</p>
        </div>
        <div class="register-wrapper" v-if="signUp">
            <form>
                <h1 class="header">Sign Up</h1>
                <input type="text" v-model="user.email" placeholder="Email" required>
                <input type="password" v-model="user.password" placeholder="Password" required>
                <input type="text" v-model="user.name" placeholder="Name" required>
                <input type="text" v-model="user.age" placeholder="Age" required>
                <div class="error" v-if="registerError">Some Field is invalid.</div>
                <button @click.prevent="register">Sign Up</button>
                <hr>
                <button class="below-btn" @click.prevent="signUp=!signUp">Sign In</button>
            </form>
        </div>
        <div class="login-wrapper" v-if="!signUp">
            <form>
                <h1 class="header">Sign In</h1>
                <input type="text" v-model="user.email" placeholder="Email" required>
                <input type="password" v-model="user.password" placeholder="Password" required>
                <div class="error" v-if="loginError">Email or password is wrong.</div>
                <button @click.prevent="login">Sign In</button>
                <hr>
                <button class="below-btn" @click.prevent="signUp=!signUp">Sign Up</button>
            </form>
        </div>
    </div>
</template>

<script>
import request from "../request/user";
export default {
    props: {
        state: {
            type: Object
        },
    },
    data: function () {
        return {
            signUp: false,
            loginError: false,
            registerError: false,
            user: {
                email: "",
                password: "",
                name: "",
                age: "",
            }
        }
    },
    methods: {
        register: async function () {
            const { user } = await request.register(this.user);
            if (!user) {
                this.registerError = true;
            } else {
                this.registerError = false;
                this.state.login = true;
            }
        },
        login: async function () {
            const { user } = await request.login({
                email: this.user.email,
                password: this.user.password
            });
            if (!user) {
                this.loginError = true;
            } else {
                this.loginError = false;
                this.state.login = true;
            }
        }
    }
}
</script>

<style scoped>
.container {
    height: 100vh;
    display: flex;
    background-color: #f0f2f5;
    padding: 20px;
}

.introduction {
    width: 50%;
    padding-left: 150px;
    padding-top: 150px;
}


.introduction > h1 {
    color: #1877f2;
    font-size: 48px;
}

.introduction p {
    font-size: 24px;
}

form > h1 {
    /* text-align: center; */
    color: #1877f2;
    font-size: 32px;
}

form {
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    width: 60%;
    margin: auto;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

input {
    padding: 10px;
    width: 100%;
    margin: 10px 0px;
    border: 1px solid #dddfe2;
    border-radius: 5px;
}

button {
    padding: 15px;
    width: 100%;
    background-color: #1877f2;
    color: #ffffff;
    font-size: 18px;
    border: 0px;
    border-radius: 5px;
    margin: 10px 0px 20px 0px;
    cursor: pointer;
}

.below-btn {
    background-color: #42b72a;
}

.register-wrapper, .login-wrapper {
    padding-top: 50px;
    width: 50%;
}

.error {
    font-weight: 700;
    color: #be4b49;
}

@media screen and (max-width: 665px) {
    .introduction {
        display: none;
    }
    .register-wrapper, .login-wrapper {
        width: 100%;
    }
    form {
        width: 100%;
    }
}

</style>