import axios from "axios";

const baseURL = "";

export default {
    getAllBlogs: async function (category, limit, skip) {
        var res;
        if (!category) {
            res = await axios.get(`${baseURL}/blogs/?limit=${limit}&skip=${skip}`);
        } else {
            res = await axios.get(`${baseURL}/blogs/${category}?limit=${limit}&skip=${skip}`);
        }
        return res.data;
    },
    getBlog: async function (id) {
        try {
            const res = await axios.get(`${baseURL}/blog/${id}`);
            return { error: undefined, blog: res.data };
        } catch (error) {
            return { error, blog: undefined };
        }
    },
    createBlog: async function (data) {
        try {
            const res = await axios.post(`${baseURL}/blog`, data, {
                headers: {
                    "Authorization": localStorage.getItem("token")
                }
            });
            return { error: undefined, blog: res.data };
        } catch (error) {
            return { error, blog: undefined };
        }
    },
    getBlogsOfUser: async function (limit, skip) {
        try {
            const res = await axios.get(`${baseURL}/blogsOfUser/?limit=${limit}&skip=${skip}`, {
                headers: {
                    "Authorization": localStorage.getItem("token")
                }
            });
            return { error: undefined, blogs: res.data };
        } catch (error) {
            return { error, blogs: undefined };
        }
    },
    deleteBlog: async function (id) {
        try {
            const res = await axios.delete(`${baseURL}/blog/${id}`, {
                headers: {
                    "Authorization": localStorage.getItem("token")
                }
            });
            return { error: undefined, blog: res.data };
        } catch (error) {
            return { error, blog: undefined };
        }
    }, 
    updateBlog: async function (id, data) {
        try {
            const res = await axios.patch(`${baseURL}/blog/${id}`, data, {
                headers: {
                    "Authorization": localStorage.getItem("token")
                }
            });
            return { error: undefined, blog: res.data };
        } catch (error) {
            return { error, blog: undefined };
        }
    }
}