<template>
    <div>
        <div class="container">
            <ul>
                <li><router-link :class="{ active: choice === 0 }" to="/">Home</router-link></li>
                <li><router-link :class="{ active: choice === 1 }" to="/my-articles">My Articles</router-link></li>
                <li><router-link :class="{ active: choice === 2 }" to="/create-blog">Create a blog</router-link></li>
                <li @click="logOut"><router-link to="/" class="logout-btn">Log out</router-link></li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        choice: {
            type: Number
        }, 
        state: {
            type: Object
        }
    },
    methods: {
        logOut: function () {
            localStorage.removeItem("token");
            this.state.login = false;
        }
    }
}
</script>

<style scoped>
.container {
    background-color: #00001a;
}

ul {
    padding: 10px 20px; 
    margin: 0px;
    position: relative;
}

li {
    display: inline-block;
}

a {
    text-decoration: none;
    display: block;
    padding: 8px;
    color: #ffffff;
    border-radius: 5px;
}

.active {
    color: #2fb5fa;
    background-color: #ffffff;
    font-weight: 700;
}

.logout-btn {
    position: absolute;
    right: 20px;
    top: 10px;
}
</style>