<template>
    <div>
        <div class="wrapper">
            <form class="create-wrapper">
                <input type="text" v-model="blog.title" class="input-field title-field" placeholder="T i t l e" required autocomplete="off">
                <textarea v-model="blog.body" class="input-field" placeholder="B o d y" required></textarea>
                <div class="checkbox">
                    <div class="checkbox-wrapper">
                        <label>Science</label>
                        <input type="checkbox" value="science" v-model="blog.categories">
                    </div>
                    <div class="checkbox-wrapper">
                        <label>Book</label>
                        <input type="checkbox" value="book" v-model="blog.categories">
                    </div>
                    <div class="checkbox-wrapper">
                        <label>Viewpoint-Argument</label>
                        <input type="checkbox" value="argument" v-model="blog.categories">
                    </div>
                    <div class="checkbox-wrapper">
                        <label>Tourism-Food</label>
                        <input type="checkbox" value="food" v-model="blog.categories">
                    </div>
                    <div class="checkbox-wrapper">
                        <label>Sport-Skill</label>
                        <input type="checkbox" value="sport" v-model="blog.categories">
                    </div>
                    <div class="checkbox-wrapper">
                        <label>Movie-Game</label>
                        <input type="checkbox" value="game" v-model="blog.categories">
                    </div>
                </div>
            </form>
            <div class="review-wrapper">
                <h1 class="review">Review</h1>
                <div>
                    <div class="review-header">
                        <h1 class="review-title">{{ blog.title }}</h1>
                        <div class="review-author">{{ user.name }}</div>
                    </div>
                    <ul class="categories">
                        <li v-for="category in blog.categories" :key="category.id">.{{ category }}</li>
                    </ul>
                    <p class="review-body">{{ blog.body }}</p>
                </div>
            </div>
            <div class="btn-wrapper">
                <button @click="submitHandler" class="submit-btn" >Submit</button>
            </div>
        </div>
    </div>
</template>

<script>
import blogRequest from "../request/blog";
export default {
    props: {
        blog: {
            type: Object
        },
        user: {
            type: Object
        },
        mode: {
            type: String
        }
    },
    methods: {
        submitHandler: async function () {
            if (this.mode === "create") {
                const { blog } = await blogRequest.createBlog(this.blog);
                this.$router.push({ name: "Blog", params: { id: blog._id } });
            } else {
                const { blog } = await blogRequest.updateBlog(this.blog._id, this.blog);
                this.$router.push({ name: "Blog", params: { id: blog._id } });
            }
        }
    },
}
</script>

<style scoped>
.wrapper {
    width: 70%;
    margin: auto;
    background-color: #ffffff;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
    padding: 10px; 
    border-radius: 5px;
}

form.create-wrapper {
    padding: 10px;
    border: 1px solid #888888;
    border-radius: 5px;
}

.input-field {
    width: 100%;
    padding: 8px;
    border: 0px;
    border-bottom: 1px solid #888888;
    margin-bottom: 10px;
    outline: none;
}

.title-field {
    font-weight: 700;
    font-size: 18px;
}

textarea {
    height: 350px;
    font-size: 14px;
}

.checkbox {
    display: flex;
    flex-wrap: wrap;
}

.checkbox-wrapper {
    margin: 10px 10px;
}

.checkbox-wrapper > label {
    font-size: 14px;
    color: #555555;
    font-weight: 600;
}

.review {
    font-size: 32px;
    color: #2fb5fa;
    text-align: center;
}

.review-header {
    display: flex;
    justify-content: space-between;
}

.review-author {
    color: #888888;
    font-size: 14px;
    font-weight: 600;
}

ul.categories li {
    display: inline-block;
    font-size: 14px;
    color: #888888;
    margin-right: 15px;
}

.btn-wrapper {
    display: flex;
    justify-content: flex-end;
    padding-right: 30px;
}

a {
    text-decoration: none;
}

.submit-btn {
    padding: 10px;
    background-color: #42b72a;
    color: #ffffff;
    font-size: 24px;
    font-weight: 600;
    border: none;
    border-radius: 6px;
    cursor: pointer;
}

@media screen and (max-width: 665px) {
    .wrapper {
        width: 100%;
    }
}
</style>