<template>
    <div>
        <RegisterAndLogin v-if="!state.login" :state="state"></RegisterAndLogin>
        <div class="container" v-if="state.login">
            <Header :choice="0" :state="state"></Header>
            <div class="main-section">
                <ul>
                    <li @click="categoryHandler('science')" :class="setActiveCate('science')" ><font-awesome-icon icon="atom"></font-awesome-icon> Science</li>
                    <li @click="categoryHandler('book')" :class="setActiveCate('book')"><font-awesome-icon icon="book-open"></font-awesome-icon> Book</li>
                    <li @click="categoryHandler('argument')" :class="setActiveCate('argument')"><font-awesome-icon icon="users"></font-awesome-icon> Viewpoint - Argument</li>
                    <li @click="categoryHandler('food')" :class="setActiveCate('food')"><font-awesome-icon icon="utensils"></font-awesome-icon> Tourism - Food</li>
                    <li @click="categoryHandler('sport')" :class="setActiveCate('sport')"><font-awesome-icon icon="running"></font-awesome-icon> Sport - Skill</li>
                    <li @click="categoryHandler('game')" :class="setActiveCate('game')"><font-awesome-icon icon="film"></font-awesome-icon> Movie - Game</li>
                </ul>
                <div class="article-list">
                    <ArticleList :articles="articles"></ArticleList>
                    <div class="see-more-btn" @click="seeMoreHandler" v-if="ableToLoadMore">See more</div>
                    <div class="loader" v-if="loader"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import RegisterAndLogin from "../views/RegisterAndLogin";
import Header from "../components/Header";
import ArticleList from "../components/ArticleList";
import request from "../request/blog";
export default {
    props: {
        state: {
            type: Object
        },
    },
    components: {
        RegisterAndLogin,
        Header,
        ArticleList,
    },
    data: function () {
        return {
            articles: [],
            limit: 7,
            skip: 0,
            currentCate: "",
            ableToLoadMore: false,
            loader: false
        }
    },
    created: async function () {
        this.loader = true;
        this.articles = await request.getAllBlogs(null, this.limit, this.skip);
        this.loader = false;
        
        // check if we can load more articles
        if (this.articles.length % this.limit !== 0 || this.articles.length === 0) {
            this.ableToLoadMore = false;
        } else {
            this.ableToLoadMore = true;
        }
    },
    methods: {
        categoryHandler: async function (category) {
            this.skip = 0;
            this.loader = true;
            this.ableToLoadMore = false;
            this.articles = [];
            if (this.currentCate !== category) {
                this.currentCate = category;
                this.articles = await request.getAllBlogs(category, this.limit, this.skip);
            } else {
                this.currentCate = "";
                this.articles = await request.getAllBlogs(null, this.limit, this.skip);
            }
            this.loader = false;

            // check if we can load more articles
            if (this.articles.length % this.limit !== 0 || this.articles.length === 0) {
            this.ableToLoadMore = false;
            } else {
                this.ableToLoadMore = true;
            }
        },
        setActiveCate: function (category) {
            return {
                'active-cate': this.currentCate === category ? true : false
            }
        },
        seeMoreHandler: async function () {
            this.skip += this.limit;
            var moreArticles;
            if (this.currentCate === "") {
                moreArticles = await request.getAllBlogs(null, this.limit, this.skip);
            } else {
                moreArticles = await request.getAllBlogs(this.currentCate, this.limit, this.skip);
            }
            this.articles = this.articles.concat(moreArticles);
            // check if we can load more articles
            if (this.articles.length % this.limit !== 0 || moreArticles.length === 0) {
                this.ableToLoadMore = false;
            } else {
                this.ableToLoadMore = true;
            }
        }
    },
}
</script>

<style scoped>
.main-section {
    background-color: #f2f2f2;
    padding: 20px;
    display: flex;
    justify-content: space-between;
}

ul {
    margin: 0px;
    padding: 0px;
    width: 20%;
    height: 100vh;
}

li {
    display: block;
    padding: 15px 10px;
    color: #99A3AD;
    cursor: pointer;
    -webkit-user-select: none;
}

.article-list {
    width: 79%;
}

font-awesome-icon {
    font-size: 20px;
}

.active-cate {
    color: #2fb5fa;
    background-color: #ffffff;
    font-weight: 700;
}

.see-more-btn {
    cursor: pointer;
    color: #2fb5fa;
    text-align: center;
}

.loader {
  border: 10px solid #ffffff; /* Light grey */
  border-top: 10px solid #000000; /* Blue */
  border-radius: 50%;
  width: 40px;
  height: 40px;
  margin: 50px auto;
  animation: spin 2s linear infinite;

}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@media screen and (max-width: 665px) {
    .main-section {
        padding-left: 5px;
        padding-right: 5px;
    }
    li {
        padding-left: 5px;
        padding-right: 5px;
    }
}
</style>