<template>
    <div>
        <Header :state="state" :choice="0"></Header>
        <div class="main-section">
            <div class="author-section">
                <img src="../../public/img/profile.png" alt="profile" class="profile-img">
                <div class="field-wrapper">
                    <span class="field-title">Email: </span>
                    <span class="field-content">{{ user.email }}</span>
                </div>
                <div class="field-wrapper">
                    <span class="field-title">Name: </span>
                    <span class="field-content" >{{ user.name }}</span>
                </div>
                <div class="field-wrapper">
                    <span class="field-title">Age: </span>
                    <span class="field-content" >{{ user.age }}</span>
                </div>
            </div>
            <div class="blog-section">
                <h1 class="blog-title" v-rainbow >{{ blog.title }}</h1>
                <ul>
                    <li v-for="category in blog.categories" :key="category.id">.{{ category }}</li>
                </ul>
                <p class="blog-body">{{ blog.body }}</p>
            </div>
        </div>
    </div>
</template>

<script>
import Header from "../components/Header";
import blogRequest from "../request/blog";
import userRequest from "../request/user";
import rainbowMixin from "../mixins/rainbowMixin";

export default {
    props: {
        state: {
            type: Object
        }
    },
    data: function () {
        return {
            user: {},
            blog: {}
        }
    },
    created: async function () {
        const { blog } = await blogRequest.getBlog(this.$route.params.id);
        const { user } = await userRequest.getUser(blog.owner);
        this.blog = blog;
        this.user = user;
    },
    components: {
        Header
    },
    mixins: [rainbowMixin]
}
</script>

<style scoped>
.main-section {
    background-color: #f2f2f2;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.author-section {
    width: 20%;
    background-color: #ffffff;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.profile-img {
    display: block;
    width: 100%;
}

.field-wrapper {
    margin-bottom: 20px;
    padding-left: 35px;
}

.field-title {
    font-weight: 700;
    color: #ed4872;
}

.field-content {
    color: #2fb5fa;
}

.blog-section {
    background-color: #ffffff;
    width: 79%;
    padding: 20px;
    padding-top: 5px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
    /* min-height: 100vh; */
}

ul {
    margin: 0px;
    margin-bottom: 25px;
}

li {
    display: inline-block;
    color: #888888;
    font-size: 14px;
    margin-right: 20px;
}

.blog-title {
    margin: 10px 0px;
}

.blog-body {
    word-break: break-word;
}

@media screen and (max-width: 665px) {
    .main-section {
        padding-right: 5px;
        padding-left: 5px;
    }

    .field-wrapper {
        padding-left: 5px;
    }
}
</style>