<template>
    <div>
        <Header :state="state"></Header>
        <div class="container">
            <CreateOrUpdateBlog :user="user" :blog="blog" mode="update"></CreateOrUpdateBlog>
        </div>
    </div>
</template>

<script>
import Header from "../components/Header";
import userRequest from "../request/user";
import blogRequest from "../request/blog";
import CreateOrUpdateBlog from "../components/CreateOrUpdateBlog";
export default {
    props: {
        state: {
            type: Object
        },
    },
    components: {
        Header,
        CreateOrUpdateBlog
    },
    data: function () {
        return {
            blog: {},
            user: {}
        }
    },
    created: async function () {
        const { user } = await userRequest.getUserFromToken();
        const { blog } = await blogRequest.getBlog(this.$route.params.id);
        this.user = user;
        this.blog = blog;
    }
}
</script>

<style scoped>
.container {
    background-color: #f2f2f2;
    padding-top: 20px;
}
</style>