<template>
    <div>
        <div v-for="article in articles" :key="article.id" class="wrapper">
            <div class="header">
                <router-link v-rainbow :to="'/blog/'+article._id" class="title">{{ article.title }}</router-link>
                <span class="author">{{ article.author }}</span>
            </div>
            <ul>
                <li v-for="category in article.categories" :key="category.id">.{{ category }}</li>
            </ul>
            <p>{{ article.body | snippet}}</p>
            <div class="modify-section" v-if="mode === 'modify'">
                <router-link class="blog-edit-btn blog-btn" :to="'/blog/update/'+article._id">Edit</router-link>
                <button class="blog-delete-btn blog-btn" @click="popupDelete(article._id)">Delete</button>
            </div>
        </div>
        <!-- popup window -->
        <div id="delete-popup-container">
            <div class="delete-popup-wrapper">
                <p>Are you sure to delete?</p>
                <div class="delete-popup-btn-wrapper">
                    <button class="delete-popup-submit-btn delete-popup-btn" @click="deleteHandler()">Submit</button>
                    <button class="delete-popup-cancel-btn delete-popup-btn" @click="popupDeleteCancel">Cancel</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import rainbowMixin from "../mixins/rainbowMixin";
import snippetMixin from "../mixins/snippetMixin";
import request from "../request/blog";
export default {
    props: {
        articles: {
            type: Array
        },
        mode: {
            type: String
        }
    },
    mixins: [rainbowMixin, snippetMixin],
    methods: {
        deleteHandler: async function () {
            const { blog } = await request.deleteBlog(this.deleteId);
            if (blog) {
                for (let i = 0; i < this.articles.length; i++) {
                    if (this.articles[i]._id === blog._id) {
                        this.articles.splice(i, 1);
                    }
                }
            }
            document.getElementById("delete-popup-container").style.display = "none"
        },
        popupDelete: function (id) {
            const style = document.getElementById("delete-popup-container").style;
            style.display = "flex";
            style.justifyContent = "center";
            style.alignItems = "center";
            this.deleteId = id;
        },
        popupDeleteCancel: function () {
            document.getElementById("delete-popup-container").style.display = "none";
        }
    },
}
</script>

<style scoped>
.wrapper {
    padding: 0px 20px 10px 20px;
    background-color: #ffffff;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
    margin-bottom: 15px;
}

ul {
    margin: 0px 0px 30px 0px;
    padding: 0px 20px;
}

li {
    display: inline-block;
    padding: 0px 15px;
    color: #99a3ad;
    font-size: 14px;
}

.header {
    margin: 0px;
    padding: 10px 0px;
    display: flex;
    justify-content: space-between;
}

.title {
    font-weight: 700;
    font-size: 24px;
}

.author {
    color: #99a3ad;
    font-weight: 700;
    font-size: 16px;
} 

.modify-section {
    display: flex;
    justify-content: flex-end;
    padding: 0px 10px 10px 0px;
}

.blog-btn {
    padding: 10px;
    border-radius: 5px;
    color: #ffffff;
    border: none;
    font-size: 14px;
}

.blog-edit-btn {
    background-color: #42b72a;
    margin-right: 20px;
}

.blog-delete-btn {
    background-color:#be4b49;
    cursor: pointer;
}

/* popup */
#delete-popup-container {
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    display: none;
    z-index: 10;
}

.delete-popup-wrapper {
    padding: 20px;
    padding-top: 10px;
    background-color: #242526;
    border-radius: 6px;
}

.delete-popup-wrapper > p {
    color: #ffffff;
    font-size: 32px;
    font-weight: 600;
}

.delete-popup-btn-wrapper {
    display: flex;
    padding: 10px;
    justify-content: flex-end;
    background-color: #242526;
}

.delete-popup-btn {
    padding: 8px 10px;
    border: none; 
    border-radius: 6px;
    cursor: pointer;
    font-size: 16px;
}

.delete-popup-submit-btn {
    margin-right: 15px;
    background-color: #be4b49;
    color: #ffffff;
}

.delete-popup-cancel-btn {
    background-color: #888888;
}

</style>